import bg11Image from 'assets/img/bg/background_1920-11.jpg';
import landingImage from 'assets/img/logo/landing-2-image.png';
import bg18Image from 'assets/img/bg/background_1920-18.jpg';
import bg1Image from 'assets/img/bg/background_640-1.jpg';
import bg3Image from 'assets/img/bg/background_640-3.jpg';
import user1Image from 'assets/img/users/100_1.png';
import { UserCard } from 'components/Card';
import Page from 'components/Page';
import React from 'react';
import { Consumer } from "../../components/Context";
/** APPS */
import Mathborana from 'components/mlugha/Mathborana';
import Mathsomali from 'components/mlugha/Mathsomali';
import Mathmaasai from 'components/mlugha/Mathmaasai';
import Engborana from 'components/mlugha/Engborana';
import Engsomali from 'components/mlugha/Engsomali';
import Engmaasai from 'components/mlugha/Engmaasai';
import Envborana from 'components/mlugha/Envborana';
import Envsomali from 'components/mlugha/Envsomali';
import Envmaasai from 'components/mlugha/Envmaasai';

import PageSpinner from 'components/PageSpinner';

import AsyncStorage from '@callstack/async-storage';
import conf from "../../api/config";
import { loginUser } from "../../api/api";

import {
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Button,
  Card,
  CardBody,
  CardImg,
  CardImgOverlay,
  CardLink,
  CardText,
  CardTitle,
  Col,
  ListGroup,
  Alert,
  ListGroupItem,
  Row,
} from 'reactstrap';
import config from '../../api/config';

class UserDashboardPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user_info:{},
            msg: '',
            error: '',
            preload:false,
            showform: true,
            showtryagain:false,
        };
        AsyncStorage.getItem(config.secret)
        .then((results) => {
          if( results !== null )
          {
            let userData = JSON.parse(results);
            this.setState({
              ...this.state,
              user_info:userData,
            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }

    camelCase = (str) => {
      let string = str.toLowerCase();
      return string.charAt(0).toUpperCase() + string.slice(1);
    }

    switchRenderModule = (param) => {
      switch(param) {
        case 'MATHBORANA':
          return <Mathborana/>
          break;
        case 'MATHSOMALI':
            return <Mathsomali/>
            break;
        case 'MATHMAASAI':
            return <Mathmaasai/>
            break;
        case 'ENGBORANA':
            return <Engborana/>
            break;
        case 'ENGSOMALI':
            return <Engsomali/>
            break;
        case 'ENGMAASAI':
            return <Engmaasai/>
            break;
        case 'ENVBORANA':
            return <Envborana/>
            break;
        case 'ENVSOMALI':
            return <Envsomali/>
            break;
        case 'ENVMAASAI':
            return <Envmaasai/>
            break;
      }
    }

    render() {
        return (
          <Consumer>
          { context => (
            <Page title="Cards" breadcrumbs={[{ name: 'cards', active: true }]}>
              <Row>
                  <Col md={12} sm={12} xs={12} className="mb-1">
                      <Card className="flex-row text-center">
                          <CardBody>
                            {this.switchRenderModule(context.state.user.app)}
                          </CardBody>
                      </Card>
                  </Col>
              </Row>
            </Page>
          )}
        </Consumer>
    );
 }
};
export default UserDashboardPage;
