import React, { useState, useCallback} from 'react';
import _123image from 'assets/img/logo/123.jpg';
import poster_img from 'assets/img/logo/poster.jpg';
import _abcimage from 'assets/img/logo/abc.jpg';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col, CardImg,Modal, ModalBody, } from 'reactstrap';
import classnames from 'classnames';
import CardBody from 'reactstrap/lib/CardBody';
import { Player, BigPlayButton, } from 'video-react';
import Iframe from 'react-iframe';
import config from '../../api/config';
import { FullScreen, useFullScreenHandle } from "react-full-screen";
import AsyncStorage from '@callstack/async-storage';
import { findMathVideos, findMathWebData} from "../../api/api";
import ExpiredComponent from 'components/mlugha/ExpiredComponent';

import { Consumer } from "../../components/Context";


const Mathmaasai = (props) => {
    const handle = useFullScreenHandle();
    const handleTwo = useFullScreenHandle();


    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if(activeTab !== tab) setActiveTab(tab);
    }

    const [activeInnerTab, setActiveInnerTab] = useState('11');

    const toggleInner = tab => {
        if(activeInnerTab !== tab) setActiveInnerTab(tab);
    }

    const [activeInnerLastTab, setActiveInnerLastTab] = useState('21');


    const toggleInnerLast = tab => {
        if(activeInnerLastTab !== tab) setActiveInnerLastTab(tab);
    }

    const camelCase = (str) => {
        let string = str.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    /** Math videos */
    const [ subscription, setSubscription ] = useState(2);
    const [ mathvideos, setMathVideos ] = useState({});
    /** Math data */
    const [ mathdata, setMathData ] = useState({
        ap1:[],
        ap2:[],
        ap3:[],
        ap4:[],
    });
    React.useEffect(() => {
        AsyncStorage.getItem(config.secret).then((data) => {
            let c = JSON.parse(data);
            let token = c.data.token;
            let app = c.data.app;
            fetchMathVideos(token, app);
            fetchMathDatas(token, app);
        }).catch((err) => console.log(err));
    }, []);

    const fetchMathVideos = (token, app) =>{
        findMathVideos(token, {app:app})
        .then((videos) => {
            if( videos.status === 200 ){
                setSubscription(videos.active);
                setMathVideos(videos.data);
                console.log('videos.data', videos.data);
            }
        }).catch((verror) => {
            console.log('error fetching videos', verror);
        });
    }
    const fetchMathDatas = (token, app) => {
        findMathWebData(token, {app:app})
        .then((datas) => {
            // console.log(datas.data.a.p1);
            if( datas.status === 200 ){
                setSubscription(datas.active);
                setMathData({
                    ...mathdata,
                    ap1: datas.data.a.p1,
                    ap2: datas.data.a.p2,
                    ap3: datas.data.a.p3,
                    ap4: datas.data.a.p4,
                });
            }
            else
            {
                return;
            }
        }).catch((derror) => {
            console.log('error fetching videos', derror);
        });
    }
    const playAudio = (id) => {
        console.log(id);
        const audioEl = document.getElementsByClassName(id)[0];
        audioEl.play();
        return;
    }
  return (
    <Consumer>
    { context => (
        <div>
            { subscription === 0 && (
                <ExpiredComponent/>
            )}

            { subscription === 1 && (
                <div>
                    <h1 className="h1-custom">{context.state.user.app_label}</h1>
                    <hr></hr>
                    <Nav tabs className="justify-content-center">
                    <NavItem>
                        <NavLink
                        className={'a-tab ' + classnames({ active: activeTab === '1' })}
                        onClick={() => { toggle('1'); }}
                        >
                        Mathematical Activities
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                        className={'a-tab ' + classnames({ active: activeTab === '2' })}
                        onClick={() => { toggle('2'); }}
                        >
                        Mathematical Animation
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                        className={'a-tab ' + classnames({ active: activeTab === '3' })}
                        onClick={() => { toggle('3'); }}
                        >
                        Mathematical Assessment
                        </NavLink>
                    </NavItem>
                    </Nav>
                    <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <br></br>
                        <div>
                            {/* Inner Tabs  */}
                            <Nav tabs className="nav-tabs-custom justify-content-center">
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerTab === '11' })}
                                onClick={() => { toggleInner('11'); }}
                                >
                                NUMBERS
                                </NavLink>
                            </NavItem>
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerTab === '12' })}
                                onClick={() => { toggleInner('12'); }}
                                >
                                OUR MONEY
                                </NavLink>
                            </NavItem>
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerTab === '13' })}
                                onClick={() => { toggleInner('13'); }}
                                >
                                COUNTING
                                </NavLink>
                            </NavItem>
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerTab === '14' })}
                                onClick={() => { toggleInner('14'); }}
                                >
                                MEASUREMENTS
                                </NavLink>
                            </NavItem>
                            </Nav>
                            <TabContent activeTab={activeInnerTab}>
                            <TabPane tabId="11">
                                <Row className="justify-content-center">
                                    <Col sm="8">
                                        {mathdata.ap1.map((pg1, idx) => (
                                            <Card key={idx} className="borderless">
                                                <CardBody>
                                                    <CardImg top src={pg1.image} />
                                                    <CardTitle><h2>{pg1.label}</h2></CardTitle>
                                                    {/* audio left */}
                                                    <audio className={'audio-npl' + idx}>
                                                        <source src={pg1.sound_left}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-npl' + idx);}} className="btn-left">{pg1.btn_left}</Button>
                                                    {/* audio right */}
                                                    <audio className={'audio-npr' + idx}>
                                                        <source src={pg1.sound_right}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-npr' + idx);}} color="primary" className="btn-right">{pg1.btn_right}</Button>
                                                </CardBody>
                                            </Card>
                                        ))}
                                    </Col>
                                </Row>
                            </TabPane>
                            <TabPane tabId="12">
                                <Row className="justify-content-center">
                                    <Col sm="8">
                                        {mathdata.ap2.map((pg2, idx) => (
                                            <Card key={idx} className="borderless">
                                                <CardBody>
                                                    <CardImg top src={pg2.image} />
                                                    <CardTitle><h2>{pg2.label}</h2></CardTitle>
                                                    {/* audio left */}
                                                    <audio className={'audio-nfl' + idx}>
                                                        <source src={pg2.sound_left}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-nfl' + idx);}} className="btn-left">{pg2.btn_left}</Button>
                                                    {/* audio right */}
                                                    <audio className={'audio-nfr' + idx}>
                                                        <source src={pg2.sound_right}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-nfr' + idx);}} color="primary" className="btn-right">{pg2.btn_right}</Button>
                                                </CardBody>
                                            </Card>
                                        ))}
                                    </Col>
                                </Row>
                            </TabPane>
                            <TabPane tabId="13">
                                <Row className="justify-content-center">
                                    <Col sm="8">
                                        {mathdata.ap3.map((pg3, idx) => (
                                            <Card key={idx} className="borderless">
                                                <CardBody>
                                                    <CardImg top src={pg3.image} />
                                                    <CardTitle><h2>{pg3.label}</h2></CardTitle>
                                                    {/* audio left */}
                                                    <audio className={'audio-nxl' + idx}>
                                                        <source src={pg3.sound_left}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-nxl' + idx);}} className="btn-left">{pg3.btn_left}</Button>
                                                    {/* audio right */}
                                                    <audio className={'audio-nxr' + idx}>
                                                        <source src={pg3.sound_right}></source>
                                                    </audio>
                                                    <Button onClick={() =>{playAudio('audio-nxr' + idx);}} color="primary" className="btn-right">{pg3.btn_right}</Button>
                                                </CardBody>
                                            </Card>
                                        ))}
                                    </Col>
                                </Row>
                            </TabPane>
                            <TabPane tabId="14">
                                <Row className="justify-content-center">
                                    <Col sm={10}>
                                        {mathdata.ap4.map((pg4, idx) => (
                                            <div key={idx} className="row justify-content-center">
                                                <Col sm="6">
                                                    <Card className="borderless">
                                                        <CardBody>
                                                            <CardImg top src={pg4.image} />
                                                            <CardTitle><h2>{pg4.label}</h2></CardTitle>
                                                            {/* audio left */}
                                                            <audio className={'audio-nvl' + idx}>
                                                                <source src={pg4.sound_left}></source>
                                                            </audio>
                                                            <Button onClick={() =>{playAudio('audio-nvl' + idx);}} className="btn-left">{pg4.btn_left}</Button>
                                                            {/* audio right */}
                                                            <audio className={'audio-nvr' + idx}>
                                                                <source src={pg4.sound_right}></source>
                                                            </audio>
                                                            <Button onClick={() =>{playAudio('audio-nvr' + idx);}} color="primary" className="btn-right">{pg4.btn_right}</Button>
                                                        </CardBody>
                                                    </Card>
                                                </Col>
                                                <Col sm="6">
                                                    <Card className="borderless">
                                                        <CardBody>
                                                            <CardImg top src={pg4.pair_image} />
                                                            <CardTitle><h2>{pg4.pair_label}</h2></CardTitle>
                                                            {/* audio left */}
                                                            <audio className={'audio-nml' + idx}>
                                                                <source src={pg4.pair_sound_left}></source>
                                                            </audio>
                                                            <Button onClick={() =>{playAudio('audio-nml' + idx);}} className="btn-left">{pg4.pair_btn_left}</Button>
                                                            {/* audio right */}
                                                            <audio className={'audio-nmr' + idx}>
                                                                <source src={pg4.pair_sound_right}></source>
                                                            </audio>
                                                            <Button onClick={() =>{playAudio('audio-nmr' + idx);}} color="primary" className="btn-right">{pg4.pair_btn_right}</Button>
                                                        </CardBody>
                                                    </Card>
                                                </Col>
                                            </div>
                                        ))}
                                    </Col>
                                </Row>
                            </TabPane>
                            </TabContent>
                            {/* End inner tabs */}
                        </div>
                    </TabPane>
                    <TabPane tabId="2">
                        <Row className="justify-content-center">
                            <Col sm="8">
                                <Card className="borderless">
                                    <CardBody>
                                        <CardText>
                                            <Player
                                                playsInline
                                                poster={poster_img}
                                                src={mathvideos.abc}>
                                                <BigPlayButton position="center" />
                                            </Player>
                                        </CardText>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="3">
                        <br></br>
                        <div>
                            {/* Inner Tab Lat  */}
                            <Nav tabs className="nav-tabs-custom justify-content-center">
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerLastTab === '21' })}
                                onClick={() => { toggleInnerLast('21'); }}
                                >
                                    Identify number of objects quiz
                                </NavLink>
                            </NavItem>
                            <NavItem className="nav-item-custom">
                                <NavLink
                                className={'a-innertab ' + classnames({ active: activeInnerLastTab === '22' })}
                                onClick={() => { toggleInnerLast('22'); }}
                                >
                                    Tap number 1-9 quiz
                                </NavLink>
                            </NavItem>
                            </Nav>
                            <TabContent activeTab={activeInnerLastTab}>
                            <TabPane tabId="21">
                                <Row className="justify-content-center">
                                    <Col sm="8">
                                        <Card className="borderless">
                                            <CardBody>
                                                <Button color="primary" onClick={handle.enter}>
                                                    Start Quiz One
                                                </Button>
                                                <FullScreen handle={handle}>
                                                    { handle.active === true && (
                                                        <Iframe width={1000} url={ config.backend_media + '/flash/' + camelCase(context.state.user.app) + '/identify/story.html'} className="iframe-custom" display="initial" position="relative"/>
                                                    )}
                                                </FullScreen>
                                                
                                            </CardBody>
                                        </Card>
                                    </Col>
                                </Row>
                            </TabPane>
                            <TabPane tabId="22">
                                <Row className="justify-content-center">
                                <Col sm="8">
                                        <Card className="borderless">
                                            <CardBody>
                                                <Button color="primary" onClick={handleTwo.enter}>
                                                    Start Quiz Two
                                                </Button>
                                                <FullScreen handle={handleTwo}>
                                                    { handleTwo.active === true && (
                                                        <Iframe width={1000} url={ config.backend_media + '/flash/' + camelCase(context.state.user.app) + '/tap/story.html'} className="iframe-custom" display="initial" position="relative"/>
                                                    )}
                                                </FullScreen>
                                                
                                            </CardBody>
                                        </Card>
                                    </Col>
                                </Row>
                            </TabPane>
                            </TabContent>
                            {/* End inner last */}
                        </div>
                    </TabPane>
                    </TabContent>
                </div>
            )}
    </div>
    )}
    </Consumer>
  );
}

export default Mathmaasai;